import React from "react"
import Link from "gatsby-link"
import styled from "styled-components"
import Helmet from "react-helmet"
import Fade from "react-reveal/Fade"
import {
  TINY_SHADOW,
  TINY_BKG,
  SECONDARY_TEXT
} from "../../components/consts/colors.js"

import "../../css/index.css"
import "../../css/typography.css"
import "../../css/Hide.css"
import SectionContainer from "../../components/SectionContainer"
import SectionContainerBackground from "../../components/SectionContainerBackground"
import SectionHeaderContent from "../../components/SectionHeaderContent"
import SectionContent from "../../components/SectionContent"
import {
  TinyButton,
  TinyButtonAlt,
  TinyButtonWhite
} from "../../components/Button.js"
import Icon from "../../components/Icons"
import SectionImages from "../../components/SectionImages"
import SectionImage from "../../components/SectionImage"
import FAQHeader from "../../components/FAQHeader"
import fathom from "../../assets/Fathom.png"

import withScreensize from "../../components/withScreensize"
import ScreensizeProvider from "../../components/ScreensizeProvider"

const pageData = {
  FAQHeadline:
    "TinyMerch uses a single embed script to give you a fully functional store",
  FAQCopy:
    "You'll simply upload a design for a shirt or poster in your TinyMerch Dashboard, configure a few simple options, such as color, size, etc,. Then you decide how much to sell your products for, and that's pretty much it. Add the script to your site and never worry about it again! No maintence, inventory, worrying about order fulfillment. We take care of it all through our trusted vendor network.",
  FAQLink: "#",
  FAQwhatIsCTA: "Get Started!"
}

const NextQ = styled.div`
  margin: 10px 0px;
  a {
    display: flex;
    transition: all 200ms ease;
    :hover {
      transform: translateX(3px);
    }
    div {
      padding-left: 10px;
      position: relative;
      top: 2px;
    }
  }
`

const HowItWorks = withScreensize(props => {
  const isMobile = props.screensize === "phones"
  const isTablet = props.screensize === "thatUglyInBetweenSize"
  const isSmall = props.screensize === "devWithConsoleOpen"
  const isWidescreen = props.screensize === "widescreenFools"

  return (
    <div>
      <Helmet title="TinyMerch | FAQ – What is TinyMerch" />
      <FAQHeader />
      <SectionContainer positionContent="center">
        <SectionContent
          justify="center"
          color
          height={
            isMobile ? (
              "300px"
            ) : isTablet ? (
              "400px"
            ) : isWidescreen ? (
              "500px"
            ) : (
              "500px"
            )
          }
          width={
            isMobile ? "95%" : isTablet ? "80%" : isWidescreen ? "40%" : "50%"
          }>
          <Fade left delay={250} duration={400}>
            <h2>{pageData.FAQHeadline}</h2>
          </Fade>
          <Fade left delay={300} duration={400}>
            <p style={{ paddingTop: "25px" }}>{pageData.FAQCopy}</p>
          </Fade>
          <Fade left delay={350} duration={400}>
            <NextQ>
              <Link to="/faq/designing-products">
                🖌️ Designing Products
                <Icon name="forward" color="#6079F2" />
              </Link>
            </NextQ>
          </Fade>
          {/* <a href={pageData.FAQLink}>
            <TinyButton>{pageData.FAQwhatIsCTA}</TinyButton>
          </a> */}
        </SectionContent>
        <SectionImages
          width={
            isMobile ? "100%" : isTablet ? "100%" : isWidescreen ? "60%" : "50%"
          }
          height={
            isMobile ? (
              "200px"
            ) : isTablet ? (
              "300px"
            ) : isWidescreen ? (
              "600px"
            ) : (
              "600px"
            )
          }>
          <Fade right delay={500} duration={750}>
            <SectionImage
              shadow
              src={fathom}
              ImgWidth={
                isMobile ? (
                  "300px"
                ) : isTablet ? (
                  "500px"
                ) : isWidescreen ? (
                  "700px"
                ) : (
                  "600px"
                )
              }
              positionTB={
                isMobile ? (
                  "10px"
                ) : isTablet ? (
                  "50px"
                ) : isWidescreen ? (
                  "80px"
                ) : (
                  "100px"
                )
              }
              positionLR={
                isMobile ? "0%" : isTablet ? "0%" : isWidescreen ? "20%" : "10%"
              }
            />
          </Fade>
        </SectionImages>
      </SectionContainer>
    </div>
  )
})

export default props => (
  <ScreensizeProvider>
    <HowItWorks {...props} />
  </ScreensizeProvider>
)



